import { Button, List, Modal } from "antd";
import { PageContainer, Row } from "components/lib";
import { useProjectById } from "pages/kanban/utils";
import { useDocumentTittle } from "utils";
import { useDeleteEpic, useEpics } from "utils/epic";
import { useEpicsQueryKey, useSearchParams } from "./utils";
import { DeleteOutlined } from '@ant-design/icons';
import dayjs from "dayjs";
import { useTasks } from "utils/task";
import { Link } from "react-router-dom";
import styled from "@emotion/styled";

export const Epic = () => {
    useDocumentTittle('任务组列表');
    const { data: currentProject } = useProjectById();
    const { data: epics } = useEpics(useSearchParams());
    const { data: tasks } = useTasks();
    const { mutate } = useDeleteEpic(useEpicsQueryKey());
    const startDelete = (id: number) => {
        Modal.confirm({
            okText: '确定',
            cancelText: '取消',
            title: '确定删除任务组吗？',
            onOk() {
                mutate({id});
            }
        });
    };
    
    return <PageContainer>
        <h1>{currentProject?.name}任务组</h1>
        <EpicContainer>
            <List
                dataSource={epics}
                itemLayout="vertical"
                renderItem={epic => <List.Item>
                    <List.Item.Meta
                        title={<Row between>
                            <span>{epic.name}</span>
                            <Button type="link" icon={<DeleteOutlined />} onClick={() => startDelete(epic.id)} />
                        </Row>}
                        description={<div>
                            <div>开始时间：{dayjs(epic.start).format('YYYY-MM-DD')}</div>
                            <div>开始时间：{dayjs(epic.end).format('YYYY-MM-DD')}</div>
                        </div>}
                    />
                    <div>
                        {tasks?.filter(task => task.epicId === epic.id).map(task => 
                            <Link
                                to={`/projects/${currentProject?.id}/kanban?editingTaskId=${task.id}`}
                                style={{ marginRight: '10px' }}
                                key={task.id}
                            >{task.name}</Link>
                        )}
                    </div>
                </List.Item>}
            />
        </EpicContainer>
    </PageContainer>
};

export const EpicContainer = styled.div`
    flex: 1;
    overflow-y: scroll;
    ::-webkit-scrollbar {
        display: none;
    }
`;