export const Mark = ({str, keyword}: {
    str: string;
    keyword: string;
}) => {
    if (!keyword) {
        return <p>{str}</p>;
    }
    const arr = str.split(keyword);
    return <p>
        {arr.map((s, i) => <span key={i}>
            {s}
            {i === arr.length - 1 ? null : <span style={{ color: 'red' }}>
                {keyword}    
            </span>}
        </span>)}
    </p>
};