import { QueryKey, useMutation, useQuery } from "react-query";
import { Task } from "types";
import { useDebonce } from "utils";
import { useHttp } from "./http";
import { useAddConfig, useDeleteConfig, useEditConfig } from "./use-optimistic-update-options";

export const useTasks = (param?: Partial<Task>) => {
    const clientHttp = useHttp();
    const debounceParam = {
        ...param,
        name: useDebonce(param?.name, 200)
    }
    
    return useQuery<Task[]>(['tasks', debounceParam], () => clientHttp('/tasks', { data: debounceParam }));
};

export const useEditTask = (queryKey: QueryKey) => {
    const clientHttp = useHttp();

    return useMutation(
        (params: Partial<Task>) => clientHttp(`/tasks/${params.id}`, {
            data: params,
            method: 'PATCH'
        }),
        useEditConfig(queryKey)
    );
};

export const useAddTask = (queryKey: QueryKey) => {
    const clientHttp = useHttp();

    return useMutation(
        (params: Partial<Task>) => clientHttp(`/tasks`, {
            data: params,
            method: 'POST'
        }),
        useAddConfig(queryKey)
    );
};

export const useDeleteTask = (queryKey: QueryKey) => {
    const clientHttp = useHttp();

    return useMutation(
        ({ id }: { id: number }) => clientHttp(`/tasks/${id}`, {
            method: 'DELETE'
        }),
        useDeleteConfig(queryKey)
    );
};

export const useGetSingleTask = (id?: number) => {
    const clientHttp = useHttp();

    return useQuery<Task>(
        ['tasks', { id }],
        () => clientHttp(`/tasks/${id}`),
        { enabled: Boolean(id) }
    );
};