import styled from "@emotion/styled";
import { Typography, List, Popover, Divider } from "antd";
import { useProjectModal } from "pages/projects-list/util";
import { useProjects } from "utils/projects";
import { NoPaddingButton } from "./lib";

export const ProjectPopover = () => {
    const { data: projects, refetch } = useProjects();
    const pinnedProjects = projects?.filter(item => item.pin);
    const { open } = useProjectModal();

    const content = <ContentDiv>
        <Typography.Text type="secondary">
            收藏项目
        </Typography.Text>
        <List>
            {
                pinnedProjects?.map(item => <List.Item key={item.id}>
                    <List.Item.Meta title={item.name} />
                </List.Item>)
            }
        </List>
        <Divider />
        <NoPaddingButton
            type="link"
            onClick={() => open()}
        >创建项目</NoPaddingButton>
    </ContentDiv>

    return <Popover onVisibleChange={() => refetch()} placement="bottom" content={content}>
        项目
    </Popover>
};

const ContentDiv = styled.div`
    min-width: 30rem;
`;