import { QueryKey, useMutation, useQuery } from "react-query";
import { Kanban } from "types";
import { useHttp } from "./http";
import { useAddConfig, useDeleteConfig } from "./use-optimistic-update-options";

export const useKanbans = (param?: Partial<Kanban>) => {
    const clientHttp = useHttp();
    
    return useQuery<Kanban[]>(['kanbans', param], () => clientHttp('/kanbans', { data: param }));
};

export const useAddKanban = (queryKey: QueryKey) => {
    const clientHttp = useHttp();

    return useMutation(
        (params: Partial<Kanban>) => clientHttp(`/kanbans`, {
            data: params,
            method: 'POST'
        }),
        useAddConfig(queryKey)
    );
};

export const useDeleteKanban = (queryKey: QueryKey) => {
    const clientHttp = useHttp();

    return useMutation(
        ({ id }: { id: number }) => clientHttp(`/kanbans/${id}`, {
            method: 'DELETE'
        }),
        useDeleteConfig(queryKey)
    );
};