import styled from "@emotion/styled";
import { Button, Drawer, Form, Input, Spin } from "antd";
import { useForm } from "antd/lib/form/Form";
import { ErrorBox } from "components/lib";
import { UserSelect } from "components/user-select";
import { useEffect } from "react";
import { useAddProject, useEditProject } from "utils/projects";
import { useProjectModal, useProjectsQueryKey } from "./util";

export const ProjectModal = () => {
    const { modalVisible, close, editProject, isLoading } = useProjectModal();
    const title = editProject ? '编辑项目' : '创建项目';
    const [form] = useForm();

    const useMutateProject = editProject ? useEditProject : useAddProject;
    const { mutateAsync, error, isLoading: mutateLoading } = useMutateProject(useProjectsQueryKey());
    const closeModal = () => {
        form.resetFields();
        close();
    };
    const onFinish = (val: any) => {
        mutateAsync({
            ...editProject,
            ...val
        }).then(() => {
            closeModal();
        });
    };

    useEffect(() => {
        form.setFieldsValue({
            ...editProject
        })
    }, [editProject, form]);

    return <Drawer forceRender width={"100vw"} visible={modalVisible} onClose={closeModal}>
        <Container>
            { isLoading ? <Spin size="large" /> : <div>
                <h1>{title}</h1>
                <ErrorBox error={error} />
                <Form form={form} layout="vertical" style={{ width: '40rem' }} onFinish={onFinish}>
                    <Form.Item
                        label="项目名称"
                        name="name"
                        rules={[
                            { required: true },
                            { message: '请输入项目名称' }
                        ]}
                    >
                        <Input placeholder="请输入项目名称" />
                    </Form.Item>
                    <Form.Item
                        label="部门"
                        name="organization"
                        rules={[
                            { required: true },
                            { message: '请输入部门名称' }
                        ]}
                    >
                        <Input placeholder="请输入部门名称" />
                    </Form.Item>
                    <Form.Item
                        label="负责人"
                        name="personId"
                    >
                        <UserSelect defaultOptionName="负责人" />
                    </Form.Item>
                    <Form.Item style={{ textAlign: 'right' }}>
                        <Button loading={mutateLoading} type="primary" htmlType="submit">提交</Button>
                    </Form.Item>
                </Form>
            </div> }
        </Container>
    </Drawer>
};

const Container = styled.div`
    display: flex;
    flex-direction: column;
    height: 80vh;
    justify-content: center;
    align-items: center;
`;