import { List } from './list';
import { SearchPanel } from './search-panel';
import { useDebonce, useDocumentTittle } from 'utils';
import { useProjects } from "utils/projects";
import { useUsers } from "utils/users";
import { useProjectModal, useProjectsSearchParam } from './util';
import { ErrorBox, NoPaddingButton, PageContainer, Row } from 'components/lib';

export const ProjectList = () => {
    useDocumentTittle('项目列表', false);

    const [params, setParams] = useProjectsSearchParam();
    const { isLoading: loading, error, data: list } = useProjects(useDebonce(params, 500));
    const { data: users } = useUsers();
    const { open } = useProjectModal();

    return <PageContainer>
        <Row between>
            <h1>项目列表</h1>
            <NoPaddingButton
                type="link"
                onClick={() => open()}
            >创建项目</NoPaddingButton>
        </Row>
        <SearchPanel params={params} setParams={setParams} users={users || []} />
        <ErrorBox error={error} />
        <List
            dataSource={list || []}
            users={users || []}
            loading={loading}
        />
    </PageContainer>
};