import { useState } from "react";
import { Login } from "./login";
import { Register } from "./register";
import { Card, Button, Divider } from 'antd';
import styled from '@emotion/styled';
import logo from 'assets/logo.svg';
import left from 'assets/left.svg';
import right from 'assets/right.svg';
import { useDocumentTittle } from "utils";
import { ErrorBox } from "components/lib";

export const UnAuthenticated = () => {
    const [registered, setRegistered] = useState(false);
    const [error, setError] = useState<Error | null>(null);
    useDocumentTittle('请登录', false);

    return <Container>
        <Header />
        <Background />
        <ShadowCard>
            <Title>{registered ? '请注册' : '请登录'}</Title>
            <ErrorBox error={error} />
            {registered ? <Register onError={setError} /> : <Login onError={setError} />}
            <Divider />
            <Button type="text" onClick={() => {
                setRegistered(!registered);
                setError(null);
            }}>
                {registered ? '已有账号，直接登录！' : '没有账号，注册新账号！'}
            </Button>
        </ShadowCard>
    </Container>
};

export const LongButton = styled(Button)`
    width: 100%;
`;

const Title = styled.h2`
    margin-bottom: 2.4rem;
    color: rgb(94, 108, 132);
`;

const Background =styled.div`
    position: absolute;
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: left bottom, right bottom;
    background-size: calc(((100vw -40rem)/2)-3.2rem), calc(((100vw -40rem)/2)-3.2rem);
    background-image: url(${left}), url(${right});
`;

const Header = styled.header`
    background: url(${logo}) no-repeat center;
    padding: 5rem 0;
    background-size: 8rem;
    width: 100%;
`;

const ShadowCard = styled(Card)`
    width: 40rem;
    min-height: 46rem;
    padding: 3.2rem 4rem;
    border-radius: .3rem;
    box-sizing: border-box;
    box-shadow: rgba(0,0,0,.1) 0 0 10px;
    text-align: center;
`;

const Container = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height: 100vh;
`;