import { useMemo } from "react";
import { useGetSingleProject } from "utils/projects";
import { useSetUrlSearchParam, useUrlQueryParam } from "utils/url";

export const useProjectsSearchParam = () => {
    const [params, setParams] = useUrlQueryParam(['name', 'personId']);
    return [
        useMemo(() => ({ ...params, personId: Number(params.personId) || undefined }), [params]),
        setParams
    ] as const;
};

export const useProjectsQueryKey = () => {
    const [params] = useProjectsSearchParam();
    return ['projects', params];
};

export const useProjectModal = () => {
    const [{ projectCreate }, setProjectCreate] = useUrlQueryParam(['projectCreate']);
    const [{ editProjectId }, setEditProjectId] = useUrlQueryParam(['editProjectId']);
    const setUrlParams = useSetUrlSearchParam()
    const { data: editProject, isLoading } = useGetSingleProject(Number(editProjectId));

    const open = () => setProjectCreate({ projectCreate: true });
    const close = () => setUrlParams({ projectCreate: "", editProjectId: "" });
    const startEdit = (id: number) => {
        setEditProjectId({ editProjectId: id });
    };

    return {
        modalVisible: projectCreate === 'true' || Boolean(editProjectId),
        open,
        close,
        startEdit,
        editProject,
        isLoading
    } as const;
};