import { useCallback, useMemo } from "react";
import { useLocation } from "react-router";
import { useGetSingleProject } from "utils/projects";
import { useGetSingleTask } from "utils/task";
import { useUrlQueryParam } from "utils/url";

export const useProjectIdInUrl = () => {
    const { pathname } = useLocation();
    const id = pathname.match(/projects\/(\d+)/)?.[1];
    return Number(id);
};

export const useProjectById = () => {
    return useGetSingleProject(useProjectIdInUrl());
};

export const useSearchParams = () => ({projectId: useProjectIdInUrl()});

export const useTasksSearchParams = () => {
    const [params] = useUrlQueryParam([
        'name', 'typeId', 'processorId', 'tagId'
    ]);
    const projectId = useProjectIdInUrl();

    return useMemo(() => ({
        projectId,
        typeId: Number(params.typeId) || undefined,
        processorId: Number(params.processorId) || undefined,
        tagId: Number(params.tagId) || undefined,
        name: params.name
    }), [projectId, params])
};

export const useKanbansQueryKey = () => ['kanbans', useSearchParams()];

export const useTasksQueryKey = () => ['tasks', useSearchParams()];

export const useTasksModal = () => {
    const [{editingTaskId}, setEditingTaskId] = useUrlQueryParam(['editingTaskId']);
    const { data: editingTask, isLoading } = useGetSingleTask(Number(editingTaskId));
    const startEdit = useCallback((id: number) => {
        setEditingTaskId({editingTaskId: id});
    }, [setEditingTaskId]);
    const close = useCallback(() => {
        setEditingTaskId({editingTaskId: ''});
    }, [setEditingTaskId]);

    return {
        editingTaskId,
        editingTask,
        startEdit,
        close,
        isLoading
    };
};