import { useAuth } from 'context/auth-context';
import qs from 'qs';
import { useCallback } from 'react';
import * as auth from './auth-provider';

const apiUrl = process.env.REACT_APP_API_URL;

interface customHttpConfig extends RequestInit {
    data?: object;
    token?: string;
}

export const http = (endPoint: string, { data, token, ...customConfig }: customHttpConfig = {}) => {
    const config = {
        method: 'GET',
        headers: {
            Authorization: token ? `Bearer ${token}` : '',
            'Content-Type': data ? 'application/json' : ''
        },
        ...customConfig
    };
    if (config.method === 'GET') {
        endPoint += `?${qs.stringify(data)}`;
    } else {
        config.body = JSON.stringify(data || {});
    }
    return fetch(`${apiUrl}${endPoint}`, config).then(async res => {
        if (res.status === 401) {
            await auth.logout();
            window.location.reload();
            return Promise.reject('请重新登录！');
        } else {
            const data = await res.json()
            if (res.ok) {
                return data;
            } else {
                return Promise.reject(data);
            }
        }
    })
};

export const useHttp = () => {
    const { user } = useAuth();
    return useCallback((...[endPoint, config]: Parameters<typeof http>) => http(endPoint, {
        ...config,
        token: user ? user.token : ''
    }), [user]);
}