import styled from "@emotion/styled";
import { Spin } from "antd";
import { PageContainer } from "components/lib";
import { useDocumentTittle } from "utils";
import { useKanbans } from "utils/kanban";
import { useTasks } from "utils/task";
import { CreateKanban } from "./create-kanban";
import { KanbanColumn } from "./kanban-column";
import { SearchPanel } from "./search-panel";
import { TaskModal } from "./task-modal";
import { useProjectById, useSearchParams, useTasksSearchParams } from "./utils";

export const Kanban = () => {
    useDocumentTittle('看板列表');

    const { data: kanbans, isLoading: kanbanLoading } = useKanbans(useSearchParams());
    const { isLoading: taskLoading } = useTasks(useTasksSearchParams());
    const loading = kanbanLoading || taskLoading;
    const { data: currentProject } = useProjectById();
    return <PageContainer>
        <h1>{currentProject?.name}看板</h1>
        <SearchPanel />
        {loading ? <Spin size="large" /> : <ColumnContainer>
            {kanbans?.map(item => <KanbanColumn kanban={item} key={item.id} />)}
            <CreateKanban />
        </ColumnContainer>}
        <TaskModal />
    </PageContainer>
};

export const ColumnContainer = styled.div`
    flex: 1;
    overflow-y: scroll;
    ::-webkit-scrollbar {
        display: none;
    }
    display: flex;
    margin-right: 2rem;
`;