import { QueryKey, useMutation, useQuery } from "react-query";
import { Project } from "types";
import { useHttp } from "./http";
import { useAddConfig, useDeleteConfig, useEditConfig } from "./use-optimistic-update-options";

export const useProjects = (param?: Partial<Project>) => {
    const clientHttp = useHttp();
    
    return useQuery<Project[]>(['projects', param], () => clientHttp('/projects', { data: param }));
};

export const useEditProject = (queryKey: QueryKey) => {
    const clientHttp = useHttp();

    return useMutation(
        (params: Partial<Project>) => clientHttp(`/projects/${params.id}`, {
            data: params,
            method: 'PATCH'
        }),
        useEditConfig(queryKey)
    );
};

export const useAddProject = (queryKey: QueryKey) => {
    const clientHttp = useHttp();

    return useMutation(
        (params: Partial<Project>) => clientHttp(`/projects`, {
            data: params,
            method: 'POST'
        }),
        useAddConfig(queryKey)
    );
};

export const useDeleteProject = (queryKey: QueryKey) => {
    const clientHttp = useHttp();

    return useMutation(
        ({ id }: { id: number }) => clientHttp(`/projects/${id}`, {
            method: 'DELETE'
        }),
        useDeleteConfig(queryKey)
    );
};

export const useGetSingleProject = (id?: number) => {
    const clientHttp = useHttp();

    return useQuery<Project>(
        ['project', { id }],
        () => clientHttp(`/projects/${id}`),
        { enabled: Boolean(id) }
    );
};