import { QueryKey, useMutation, useQuery } from "react-query";
import { Epic } from "types";
import { useHttp } from "./http";
import { useAddConfig, useDeleteConfig } from "./use-optimistic-update-options";

export const useEpics = (param?: Partial<Epic>) => {
    const clientHttp = useHttp();
    
    return useQuery<Epic[]>(['epics', param], () => clientHttp('/epics', { data: param }));
};

export const useAddEpic = (queryKey: QueryKey) => {
    const clientHttp = useHttp();

    return useMutation(
        (params: Partial<Epic>) => clientHttp(`/epics`, {
            data: params,
            method: 'POST'
        }),
        useAddConfig(queryKey)
    );
};

export const useDeleteEpic = (queryKey: QueryKey) => {
    const clientHttp = useHttp();

    return useMutation(
        ({ id }: { id: number }) => clientHttp(`/epics/${id}`, {
            method: 'DELETE'
        }),
        useDeleteConfig(queryKey)
    );
};