import styled from "@emotion/styled";
import { Button, Dropdown, Menu } from "antd";
import { NoPaddingButton, Row } from "components/lib";
import { useAuth } from "context/auth-context";
import { ProjectList } from "pages/projects-list";
import { ReactComponent as SoftwareLogo } from "assets/software-logo.svg";
import { resetRoute } from "utils";
import { Navigate, Route, Routes } from 'react-router';
import { BrowserRouter as Router } from 'react-router-dom';
import { ProjectItem } from "pages/project-item";
import { ProjectModal } from "pages/projects-list/project-modal";
import { ProjectPopover } from "components/project-popover";
import { UserPopover } from "components/user-popover";

const PageHeader = () => {
    const { logout, user } = useAuth();
    return <Header between>
        <HeaderLeft gap>
            <NoPaddingButton type="link" onClick={resetRoute}>
                <SoftwareLogo width="18rem" color="rgb(38, 132, 255)" />
            </NoPaddingButton>
            <ProjectPopover />
            <UserPopover />
        </HeaderLeft>
        <Dropdown overlay={
            <Menu>
                <Menu.Item key="logout">
                    <Button type="link" onClick={logout}>登出</Button>
                </Menu.Item>
            </Menu>
        }>
            <Button type="link" onClick={e => e.preventDefault()}>
                Hi, {user?.name}
            </Button>
        </Dropdown>
    </Header>
};

export const Authenticated = () => {
    return <Container>
        <Router>
            <PageHeader />
            <Main>
                <Routes>
                    <Route path="/projects" element={<ProjectList />} />
                    <Route path="/projects/:id/*" element={<ProjectItem />} />
                    <Route index element={<Navigate to="/projects" replace={true} />} />
                </Routes>
            </Main>
            <ProjectModal />
        </Router>
    </Container>
};

const Container = styled.div`
    display: grid;
    grid-template-rows: 6rem 1fr;
    height: 100vh;
`;

const Header = styled(Row)`
    padding: 3.2rem;
    box-shadow: 0 0 5px 0 rgba(0, 0, 0, .1);
    z-index: 1;
`;

const HeaderLeft = styled(Row)``;

const Main = styled.div`
    overflow: hidden;
    flex: 1;
    display: flex;
`;