import { Kanban, Task } from "types";
import { useTasks } from "utils/task";
import { useKanbansQueryKey, useTasksModal, useTasksSearchParams } from "./utils";
import taskIcon from 'assets/task.svg';
import bugIcon from 'assets/bug.svg';
import { useTaskTypes } from "utils/task-types";
import styled from "@emotion/styled";
import { Card, Modal, Menu, Button, Dropdown } from "antd";
import { DashOutlined } from '@ant-design/icons';
import { CreateTask } from "./create-task";
import { Mark } from "components/mark";
import { useDeleteKanban } from "utils/kanban";
import { Row } from "components/lib";

export const TaskTypeIcon = ({id}: {id: number}) => {
    const { data: taskTypes } = useTaskTypes();
    const name = taskTypes?.find(type => type.id === id)?.name;

    return name ? <img alt="" src={name === 'task' ? taskIcon : bugIcon} /> : null;
};

const TaskCard = ({item}: {item: Task}) => {
    const { startEdit } = useTasksModal();
    const { name: keyword } = useTasksSearchParams();

    return <Card
        style={{ marginBottom: '.5rem', cursor: 'pointer' }}
        onClick={() => startEdit(item.id)}
    >
        <Mark str={item.name} keyword={keyword} />
        <TaskTypeIcon id={item.typeId} />
    </Card>
};

const More = ({kanban}: {kanban: Kanban}) => {
    const { mutate } = useDeleteKanban(useKanbansQueryKey());
    const startDelete = () => {
        Modal.confirm({
            okText: '确定',
            cancelText: '取消',
            title: '确定删除看板吗？',
            onOk() {
                mutate({id: kanban.id});
            }
        });
    };
    const overlay = <Menu>
        <Menu.Item>
            <Button type="link" onClick={startDelete}>
                删除
            </Button>
        </Menu.Item>
    </Menu>;

    return <Dropdown overlay={overlay}>
        <Button type="link" icon={<DashOutlined />} />
    </Dropdown>
};

export const KanbanColumn = ({kanban}: {kanban: Kanban}) => {
    const { data: allTasks } = useTasks(useTasksSearchParams());
    const tasks = allTasks?.filter(item => item.kanbanId === kanban.id);

    return <Container>
        <Row between>
            <h3>{kanban.name}</h3>
            <More kanban={kanban} />
        </Row>
        <TaskContainer>
            {tasks?.map(item => <TaskCard item={item} key={item.id} />)}
            <CreateTask kanbanId={kanban.id} />
        </TaskContainer>
    </Container>
};

export const Container = styled.div`
    flex: 1;
    min-width: 20rem;
    border-radius: 6px;
    background-color: rgb(244, 245, 247);
    overflow: scroll;
    ::-webkit-scrollbar {
        display: none;
    }
    display: flex;
    flex-direction: column;
    padding: .7rem .7rem 1rem;
    margin-right: 1.5rem;
`;

const TaskContainer = styled.div`
    overflow: scroll;
    ::-webkit-scrollbar {
        display: none;
    }
`;