import { useEffect, useRef, useState } from "react";

export const ifFalsy = (value: unknown) => value === 0 ? false : !value;
export const ifVoid = (value: unknown) => value === undefined || value === null || value === '';

export const cleanObject = (object: { [key: string]: unknown }) => {
    const result = { ...object };
    Object.keys(object).forEach(key => {
        const value = object[key];
        if (ifVoid(value)) {
            delete result[key];
        }
    })
    return result;
};

export const useMounted = (callback: () => void) => {
    useEffect(() => {
        callback();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);
};

export const useDebonce = <V>(value: V, delay: number) => {
    const [debonceValue, setDebonceValue] = useState(value);
    useEffect(() => {
        const timeout = setTimeout(() => setDebonceValue(value), delay);
        return () => clearTimeout(timeout);
    }, [value, delay]);
    return debonceValue;
};

export const useArray = <T>(persons: T[]) => {
    const [value, setValue] = useState(persons);

    const clear = () => {
        setValue([]);
    };

    const removeIndex = (index: number) => {
        const arr = [ ...value ];
        arr.splice(index, 1);
        setValue(arr);
    };

    const add = (item: T) => {
        const arr = [ ...value ];
        arr.push(item);
        setValue(arr);
    };

    return {
        value,
        clear,
        removeIndex,
        add
    }
};

export const useDocumentTittle = (title: string, keepOnUnmounted: boolean = true) => {
    const oldTitle = useRef(document.title).current;
    
    useEffect(() => {
        document.title = title;
    }, [title]);

    useEffect(() => {
        return () => {
            if (!keepOnUnmounted) {
                document.title = oldTitle;
            }
        }
    }, [oldTitle, keepOnUnmounted]);
};

export const resetRoute = () => {
    window.location.href = window.location.origin;
};

export const useMountedRef = () => {
    const mountedRef = useRef(false);
    useEffect(() => {
        mountedRef.current = true;
        return () => {
            mountedRef.current = false;
        }
    })
    return mountedRef;
};