import { Modal, Form, Input, Button } from "antd";
import { useForm } from "antd/lib/form/Form";
import { TaskTypesSelect } from "components/task-types-select";
import { UserSelect } from "components/user-select";
import { useEffect } from "react";
import { useDeleteTask, useEditTask } from "utils/task";
import { useTasksModal, useTasksQueryKey } from "./utils";

const layout = {
    labelCol: { span: 8 },
    wrapperCol: { span: 16 }
};

export const TaskModal = () => {
    const [form] = useForm();
    const { editingTaskId, editingTask, close } = useTasksModal();
    const { mutateAsync: editTask, isLoading: editLoading } = useEditTask(useTasksQueryKey());
    const { mutate: deleteTask } = useDeleteTask(useTasksQueryKey());
    const onCancel = () => {
        close();
        form.resetFields();
    };
    const onOk = async () => {
        await editTask({
            ...editTask,
            ...form.getFieldsValue()
        });
        close();
    };
    const startDelete = () => {
        Modal.confirm({
            okText: '确定',
            cancelText: '取消',
            title: '确定删除任务吗？',
            onOk() {
                deleteTask({id: Number(editingTaskId)});
            }
        });
        close();
    };
    useEffect(() => {
        form.setFieldsValue(editingTask);
    }, [form, editingTask]);

    return <Modal
        transitionName=""
        maskTransitionName=""
        okText="确认"
        cancelText="取消"
        confirmLoading={editLoading}
        title="编辑任务"
        onCancel={onCancel}
        onOk={onOk}
        forceRender
        visible={!!editingTaskId}
    >
        <Form
            {...layout}
            initialValues={editingTask}
            form={form}
        >
            <Form.Item
                label="任务名"
                name="name"
                rules={[
                    {required: true},
                    {message: '请输入名称'}
                ]}
            >
                <Input />
            </Form.Item>
            <Form.Item
                label="经办人"
                name="processorId"
            >
                <UserSelect defaultOptionName="经办人" />
            </Form.Item>
            <Form.Item
                label="类型"
                name="typeId"
            >
                <TaskTypesSelect defaultOptionName="类型" />
            </Form.Item>
            <Form.Item
                name="id"
                hidden
            />
            <div style={{ textAlign: 'right' }}>
                <Button
                    style={{ fontSize: '12px' }}
                    size="small"
                    onClick={startDelete}
                >删除</Button>
            </div>
        </Form>
    </Modal>
};