import { Dropdown, Table, TableProps, Menu, Modal } from 'antd';
import { DashOutlined } from '@ant-design/icons';
import dayjs from "dayjs";
import { Link } from "react-router-dom";
import { Pin } from "components/pin";
import { useDeleteProject, useEditProject } from "utils/projects";
import { NoPaddingButton } from "components/lib";
import { useProjectModal, useProjectsQueryKey } from "./util";
import { Project, User } from "types";

interface ListProps extends TableProps<Project> {
    users: User[];
};

export const List = ({ users, ...props }: ListProps) => {
    const { mutate } = useEditProject(useProjectsQueryKey());

    return <Table
        rowKey="id"
        pagination={false}
        columns={[
            {
                title: <Pin checked disabled />,
                render(value, item) {
                    return <Pin checked={item.pin} onCheckedChange={e => {
                        mutate({ id: item.id, pin: e });
                    }} />
                }
            },
            {
                title: '项目名称',
                sorter: (a, b) => a.name.localeCompare(b.name),
                render(value, item) {
                    return <Link to={String(item.id)}>{item.name}</Link>
                }
            },
            {
                title: '项目名称',
                dataIndex: 'organization'
            },
            {
                title: '管理人',
                render(value, project) {
                    return <span>
                        {users.find(i => i.id === project.personId)?.name}
                    </span>
                }
            },
            {
                title: '创建时间',
                render(value, project) {
                    return <span>
                        {project.created ? dayjs(project.created).format('YYYY-MM-DD') : 'NULL'}
                    </span>
                }
            },
            {
                render(value, item) {
                    return <ActionsGruop item={item} />
                }
            }
        ]}
        { ...props }
    />
};

const ActionsGruop = ({ item }: { item: Project }) => {
    const { startEdit } = useProjectModal();
    const editProject = (id: number) => () => startEdit(id);
    const { mutate: deleteProject } = useDeleteProject(useProjectsQueryKey());
    const confirmDeleteProject = (id: number) => {
        Modal.confirm({
            title: '确定删除该项目吗？',
            content: '点击确定删除',
            okText: '确定',
            onOk() {
                deleteProject({id});
            }
        });
    };

    return <Dropdown overlay={
        <Menu>
            <Menu.Item key="edit">
                <NoPaddingButton
                    type="link"
                    onClick={editProject(item.id)}
                >编辑</NoPaddingButton>
            </Menu.Item>
            <Menu.Item key="delete">
                <NoPaddingButton
                    type="link"
                    onClick={() => confirmDeleteProject(item.id)}
                >删除</NoPaddingButton>
            </Menu.Item>
        </Menu>
    }>
        <NoPaddingButton type="link" icon={<DashOutlined />} />
    </Dropdown>
};